import { RendererCanvas } from '../../workshop-vector/base/RendererCanvas';

export class Solution extends RendererCanvas {
    t: number = 0.0;
    pMap: PixelMap = undefined;
    constructor(id: string) {
        super(document.getElementById(id) as HTMLDivElement);
        this.ctx.translate(this.ctx.canvas.width * 0.5, this.ctx.canvas.height * 0.5);
        this.init();
    }
    public init() {
        const size = 200
        this.pMap = new PixelMap(new Vector3(-size, -size, 0), new Vector3(size, size, 0) ,10, 10)
        this.start();
    }
    public render(ctx: CanvasRenderingContext2D): void {

        this.pMap.render(ctx);
    }
    public mouseMove(x: number, y: number): void {
        this.pMap.picking(x - this.ctx.canvas.width * 0.5, y - this.ctx.canvas.height * 0.5)
    }

    public mouseClick(x: number, y: number): void {
        //
    }
}

class PixelMap {
    public pixelList: Pixel[] = []
    public minV: Vector3;
    public maxV: Vector3;
    public xNum = 10;
    public yNum = 10;
    public intervalX;
    public intervalY;
    constructor(minV: Vector3, maxV: Vector3, xNum: number, yNum: number) {
        this.minV = minV;
        this.maxV = maxV;
        this.xNum = xNum;
        this.yNum = yNum;
        this.init()
    }
    public init(){
        this.pixelList = []
        const x = this.minV.x
        const y = this.minV.y

        this.intervalX = (this.maxV.x - this.minV.x) / (this.xNum + 1)
        this.intervalY = (this.maxV.y - this.minV.y) / (this.yNum + 1)
        
        for(let j = 0 ; j < this.yNum; j++) {
            for(let i = 0 ; i < this.xNum; i++) {
                const xV = x + i * this.intervalX;
                const yV = y + j * this.intervalY;

                let pix = new Pixel(this, xV , yV , 0);
                this.pixelList.push(pix);
            }
        }

    }
    public picking(x: number, y: number) {
        const mv = new Vector3(x,y,0)
        let dis = Number.MAX_VALUE;
        let id = -1
        for(let i = 0 ; i < this.pixelList.length; ++i) {
           const theDis = distance(this.pixelList[i].vec, mv)
           if (dis > theDis) {
            dis = theDis
            id = i;
           }
           this.pixelList[i].hover = false
        }
        if (id) {
            this.pixelList[id].hover = true
        }
    }
    public render(ctx) {
        // console.log(this.pixelList.length)
        this.pixelList.forEach((p: Pixel) => {
            p.render(ctx);
        })
    }
}

class Pixel {
    public pMap: PixelMap;
    public vec: Vector3
    public hover: boolean = false;
    constructor(pMap: PixelMap, x: number, y: number, z: number) {
        this.pMap = pMap;
        this.vec = new Vector3(x,y,z)
    }
    public render(ctx: CanvasRenderingContext2D) {
        const width = this.pMap.intervalX 
        const height = this.pMap.intervalY

        ctx.beginPath()
        ctx.rect(this.vec.x - width * 0.5, this.vec.y - height * 0.5, width, height)
        ctx.closePath()
        ctx.stroke()
        if (this.hover) {
            ctx.fillStyle = '#555';
            ctx.fill()
        } 

        ctx.beginPath();
        ctx.arc(this.vec.x, this.vec.y, this.vec.r, 0, Math.PI * 2.0);
        ctx.closePath();
        if (this.hover) {
            ctx.fillStyle = '#999';
        } else {
            ctx.fillStyle = '#333';
        }
        ctx.fill();
    }
}

class Vector3 {
    public x: number = 0.0;
    public y: number = 0.0;
    public z: number = 0.0;
    public r: number = 3
    constructor(x,y,z) {
        this.x = x;
        this.y = y;
        this.z = z;
    }
}


const distance = (v0: Vector3, v1: Vector3) => {
    return Math.sqrt(distance2(v0, v1));
}
const distance2 = (v0: Vector3, v1: Vector3) => {
    return (v0.x - v1.x) * (v0.x - v1.x) + (v0.y - v1.y) * (v0.y - v1.y) + (v0.z - v1.z) * (v0.z - v1.z);
}